.index {
  margin: auto;
  width: 100%;
  height: 85%;
  display: grid;
  section {
    width: 90%;
    height: 100%;
    margin: auto;
    align-self: center;
    display: grid;
    background-image: url("../assets/img/artwork.svg");
    background-position: center right;
    background-size: 75%;
    background-repeat: no-repeat;
    div.description {
      align-self: center;
      display: block;
      width: 500px;
      h1 {
        font-weight: 700;
        font-size: 50px;
        line-height: 130%;
        color: $primary_color;
        letter-spacing: 1px;
      }
      h2 {
        font-weight: 500;
        font-size: 30px;
        line-height: 130%;
        color: $primary_color;
        margin: 30px 0;
        letter-spacing: 1px;
        color: $secondary_color;
      }
      p {
        font-size: 17px;
        line-height: 180%;
        color: rgba(0, 0, 0, 0.5);
        letter-spacing: 1px;
      }
    }
  }
}
